'use client';

import { useState, useEffect } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { getOrderBookData } from '@/lib/actions';
import type { OrderBook, OrderBookEntry } from '@/lib/bybit-api';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';

interface OrderBookProps {
  symbol: string;
}

export function OrderBook({ symbol }: OrderBookProps) {
  const [data, setData] = useState<OrderBook | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const orderBookData = await getOrderBookData(symbol);
        if (orderBookData) {
          setData(orderBookData);
          setError(null);
        } else {
          throw new Error('No order book data received.');
        }
      } catch (e) {
        const errorMessage = e instanceof Error ? e.message : 'An unknown error occurred.';
        setError(errorMessage);
        console.error(`Failed to fetch order book for ${symbol}:`, e);
      } finally {
        setIsLoading(false);
      }
    };

    setIsLoading(true);
    setData(null);
    setError(null);

    fetchData();
    const intervalId = setInterval(fetchData, 5000); // Poll every 5 seconds

    return () => clearInterval(intervalId);
  }, [symbol]);

  const renderRows = (entries: OrderBookEntry[] | undefined, type: 'bid' | 'ask') => {
    if (!entries) return null;
    
    const sortedEntries = type === 'ask' ? [...entries].reverse() : entries;

    let cumulativeTotal = 0;
    const dataWithTotal = sortedEntries.map(([price, size]) => {
        cumulativeTotal += parseFloat(size);
        return [parseFloat(price), parseFloat(size), cumulativeTotal];
    });
    
    const maxTotal = dataWithTotal.length > 0 ? dataWithTotal[dataWithTotal.length - 1][2] : 0;

    return dataWithTotal.map(([price, size, total], index) => {
      const depth = maxTotal > 0 ? (total / maxTotal) * 100 : 0;
      const bgClass = type === 'bid' ? 'bg-green-500/10' : 'bg-red-500/10';
      return (
        <TableRow key={index} className="text-xs">
          <TableCell className="p-1 font-mono text-left relative" style={{ color: type === 'bid' ? 'hsl(var(--chart-2))' : 'hsl(var(--chart-5))' }}>
              <div className={`absolute right-0 top-0 h-full ${bgClass}`} style={{ width: `${depth}%`, zIndex: 0 }}></div>
              <span className="relative z-10">{price.toFixed(2)}</span>
          </TableCell>
          <TableCell className="p-1 font-mono text-right z-10">{size.toFixed(4)}</TableCell>
          <TableCell className="p-1 font-mono text-right z-10">{total.toFixed(4)}</TableCell>
        </TableRow>
      );
    });
  };
  
  const LoadingRows = () => (
    Array.from({ length: 10 }).map((_, i) => (
        <TableRow key={i} className="text-xs">
            <TableCell className="p-1"><Skeleton className="h-4 w-full" /></TableCell>
            <TableCell className="p-1"><Skeleton className="h-4 w-full" /></TableCell>
            <TableCell className="p-1"><Skeleton className="h-4 w-full" /></TableCell>
        </TableRow>
    ))
  );

  const ErrorRow = ({ message }: { message: string }) => (
    <TableRow>
      <TableCell colSpan={3} className="text-center text-red-500 p-4">
        Error: {message}
      </TableCell>
    </TableRow>
  );

  return (
    <Card>
      <CardHeader>
        <CardTitle>Order Book</CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[200px]">
          <div className="grid grid-cols-1">
            <Table>
              <TableHeader>
                <TableRow className="text-xs">
                  <TableHead className="p-1 text-left">Price (USDT)</TableHead>
                  <TableHead className="p-1 text-right">Size ({symbol.replace('USDT', '')})</TableHead>
                  <TableHead className="p-1 text-right">Total</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {isLoading ? <LoadingRows/> : error ? <ErrorRow message={error} /> : renderRows(data?.asks, 'ask')}
              </TableBody>
            </Table>
            <div className="text-lg font-bold text-center py-2 border-t border-b">
              {isLoading ? <Skeleton className="h-6 w-1/4 mx-auto" /> : error ? 'N/A' : data?.bids?.[0]?.[0] ? parseFloat(data.bids[0][0]).toFixed(2) : 'N/A' }
            </div>
            <Table>
              <TableBody>
                {isLoading ? <LoadingRows/> : error ? <ErrorRow message={error} /> : renderRows(data?.bids, 'bid')}
              </TableBody>
            </Table>
          </div>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
