'use client';

import { useState } from 'react';
import { Card, CardHeader, CardTitle, CardContent, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Bot, Zap } from 'lucide-react';
import { getAiTradingSignal } from '@/lib/actions';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

interface AiSignalProps {
  symbol: string;
  timeframe: string;
}

export function AiSignal({ symbol, timeframe }: AiSignalProps) {
  const [isLoading, setIsLoading] = useState(false);
  const [analysis, setAnalysis] = useState('');
  const { toast } = useToast();

  const handleGenerateSignal = async () => {
    setIsLoading(true);
    setAnalysis('');
    const result = await getAiTradingSignal(symbol, timeframe);
    if (result.success && result.analysis) {
      setAnalysis(result.analysis);
    } else {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: result.error || 'Failed to generate signal.',
      });
    }
    setIsLoading(false);
  };

  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between">
        <div className="flex items-center gap-2">
          <Bot className="h-5 w-5" />
          <CardTitle>AI Trading Signal</CardTitle>
        </div>
        <Button onClick={handleGenerateSignal} disabled={isLoading} className="bg-accent hover:bg-accent/90 text-accent-foreground">
          <Zap className="mr-2 h-4 w-4" />
          {isLoading ? 'Generating...' : 'Generate Signal'}
        </Button>
      </CardHeader>
      <CardContent>
        {isLoading && (
          <div className="space-y-4">
            <Skeleton className="h-4 w-3/4" />
            <Skeleton className="h-4 w-full" />
            <Skeleton className="h-4 w-5/6" />
            <Skeleton className="h-4 w-1/2" />
          </div>
        )}
        {!isLoading && !analysis && (
          <div className="text-center text-muted-foreground py-8">
            <p>Click "Generate Signal" to get AI-powered market analysis.</p>
          </div>
        )}
        {analysis && (
          <div className="prose prose-sm dark:prose-invert max-w-none">
            <ReactMarkdown
              remarkPlugins={[remarkGfm]}
              components={{
                h1: ({node, ...props}) => <h1 className="text-2xl font-bold my-4" {...props} />,
                h2: ({node, ...props}) => <h2 className="text-xl font-semibold my-3" {...props} />,
                h3: ({node, ...props}) => <h3 className="text-lg font-semibold my-2" {...props} />,
                p: ({node, ...props}) => <p className="leading-relaxed mb-4" {...props} />,
                ul: ({node, ...props}) => <ul className="list-disc pl-5 mb-4 space-y-1" {...props} />,
                strong: ({node, ...props}) => <strong className="font-semibold" {...props} />,
              }}
            >
              {analysis}
            </ReactMarkdown>
          </div>
        )}
      </CardContent>
      <CardFooter>
          <p className="text-xs text-muted-foreground">AI analysis is for informational purposes only and not financial advice.</p>
      </CardFooter>
    </Card>
  );
}
